*{
  margin: 0;
  padding: 0;
}
body{
  background-color: #676464;
}

.todo-main{
  /* border: 2px solid black; */
  height: 97vh;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}


.todo-sub{
  /* border: 2px solid blue; */
  width: 75%;
  height: 80%;
}

.input-Container{
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.todo-input{
  margin-top: 16px;
  margin-left: 10px;
  width: 70%;
  height: 40px;
  border-radius: 14px;
  border:1px solid black;
  padding-left: 10px;  
  box-shadow: 0px 10px 20px rgba(0,0,0,0.3);
  outline: none;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: large;
}


.add-button{
  margin-top: 16px;
  width:40px;
  height:40px;
  border-radius: 50%;
  border:none;
  background-color:#316fc1;
  color: white;
  font-weight: bolder;
  font-size: larger;
  margin-left: 10px;  
  cursor: pointer;
  box-shadow: 0px 5px 10px rgba(11, 10, 10, 0.3);
  transition: 0.3s;
}


.list-item{
  list-style-type: none;
  /* background-color: #596e8b; */
  border:2px solid rgb(161, 158, 158);
  color:white;
  padding:3px;
  padding-left: 5px;
  border-radius: 5px; 
  width: 98%;
  height: 30px;
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 10px;
}


.icons{
  position: absolute;
  right: 10px;
}


.icons i{
  margin: 4px;
  font-size: large;
  cursor: pointer;
  transition: 0.1s;   
}
.icons i:hover{
  transform: scale(1.2);  
}
.icon-delete:hover{
  color: red;
}

